:host *,
:host ::before,
:host ::after,
.scribe *,
.scribe ::before,
.scribe ::after {
    box-sizing: border-box;
    background-repeat: no-repeat;
}

:host,
.scribe {
    container-type: inline-size;
    container-name: host;

    font-family: Jazz, JazzText, sans-serif;

    /* Metrics */
    --bar-line-size:     0.05rem;
    /* Compress column-gap below 800px viewport to fit more information
       on mobile (0.08vw becomes 0.04em at 800px viewport). */
    --bar-column-gap:    min(0.08vw, 0.02em);
    --bar-padding-left:  0.3em;
    --bar-padding-right: 0.15em;
    /* Widths of note heads in em (measured and overridden on render) */
    --head-1-size:       0.337402;
    --head-1-size:       0.390137;
    --head-1-size:       0.455078;
    /* Space around accidentals */
    --acci-margin-left:  0.0875;
    --acci-margin-right: 0.0833;
    /* Space around notes */
    --note-margin-left:  0;
    --note-margin-right: 0;
    /* Space around rests */
    --rest-margin-left:  0;
    --rest-margin-right: 0;
    /* Default height of stem */
    --stem-height:     1;
    /* Thickness of stem */
    --stem-width:      0.033;
    /* Alignment of root of stem relative to baseline */
    --stem-align-up:   0.03;
    --stem-align-down: -0.04;
    /* Width of signature column */
    --signature-width: 2em;

    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    column-gap: 0;
    align-content: start;

    /* Encourage scribe not to stretch vertically when in a grid context – it
       exposes signature staves. Hmm, no don't do this, it causes problems when
       in flex with flex direction row. */
    /* align-self: start; */

    /* Make sure bars do not stretch vertically */
    align-items: start;
    z-index: 1;

    height: auto;
    padding-top: 1em;
    padding-bottom: 1em;
    padding-left: calc(var(--signature-width) - 1px);
}

:host::after,
.scribe::after {
    /* Control spread of last line */
    content: '';
    flex: 6 1;
}

:host([clef="drum"]) {
    --signature-width: 1em;
}

:host > .side,
.scribe > .side {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    padding-top: 1em;
    min-width: var(--signature-width);
    grid-column: 1;
    grid-row: 1;
    justify-self: stretch;
    align-self: stretch;
    max-height: 100%;
}



.side {
    z-index: 2;
    align-content: start;
    height: 100%;
    overflow: hidden;
}

.side > .signature-stave {
    width: auto;
    min-width: 100%;
}

.side > .bar + .bar > .clef {
    display: block;
}

:host > .bar,
.scribe > .bar {
    flex: 1 0 min-content;
    /* Make bar lines overlap */
    margin-left: calc(-1 * var(--bar-line-size, 1px));
}


svg {
    fill: currentcolor;
    /*background-color: rgba(200, 180, 30, 0.5);*/
}

svg > path {
    fill: inherit;
}


